.account-settings .user-profile {
    margin: 0 0 1rem 0;
    padding-top: 40px;
    padding-bottom: 20px;
    text-align: center;
}
.account-settings .user-profile .user-avatar {
    margin: 0 0 1rem 0;
}
.account-settings .user-profile .user-avatar img {
    width: 200px;
    height: 200px;
    -webkit-border-radius: 100px;
    -moz-border-radius: 100px;
    border-radius: 100px;
}
.account-settings .user-profile h5.user-name {
    margin: 0 0 0.5rem 0;
}
.account-settings .user-profile h6.user-email {
    margin: 0;
    font-size: 0.8rem;
    font-weight: 400;
}
.account-settings .about {
    margin: 1rem 0 0 0;
    font-size: 0.8rem;
    text-align: center;
}
.card {
    background: #272E48;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    border-radius: 5px;
    border: 0;
    margin-bottom: 1rem;
}
.form-control:disabled {
    border: 1px solid #596280;
    -webkit-border-radius: 2px;
    -moz-border-radius: 2px;
    border-radius: 2px;
    font-size: 1rem;
    background: #d8d8d8;
    color: rgba(0,0,0,0.5);
}

.form-control:active {
    border: 1px solid #596280;
    -webkit-border-radius: 2px;
    -moz-border-radius: 2px;
    border-radius: 2px;
    font-size: 1rem;
    background: #ffffff;
    color: rgba(0,0,0);
}

.user-name, .user-email, .about, .labels{
    color: #fff;
}

.form-control::placeholder{
    color: #fff;
    opacity: 0.5;
}

#btnTriggers{
    margin-bottom: 30px;
}


label.labelsss input[type="file"] {
    position: absolute;
    top: -1000px;
  }
  .labelsss {
    cursor: pointer;
    border: 1px solid #cccccc;
    border-radius: 5px;
    padding: 5px 15px;
    margin: 5px;
    background: #dddddd;
    display: inline-block;
    transition: 0.25s ease;
  }
  .labelsss:hover {
    background: rgb(173, 172, 172);
  }
  .labelsss:active {
    background: #9fa1a0;
  }
  .labelsss:invalid+span {
    color: #000000;
  }
  .labelsss:valid+span {
    color: #ffffff;
  }