#cards{
    border-top: 1px solid lightgray;
    border-bottom: 1px solid lightgray;
    margin-top: 30px;
    padding-left: 70px;
    padding-top: 30px;
    padding-bottom: 30px;
}

.card{
    box-shadow: 0 10px 14px rgba(0, 0, 0, 0.2);
    width: 15rem;
}

.card:hover{
    box-shadow: 0 10px 14px rgba(0, 0, 0, 0.5);
}

.cardStyling{
    border-top: 1px solid lightgray;
    border-left: 1px solid lightgray;
    border-bottom: 1px solid lightgray;
    margin-top: 30px;
}

.catalogueQuotes{
    font-size: 15pt;
}

#adsTitle{
    color: rgba(139, 139, 139, 0.514);
    
}

.ads{
    margin-bottom: 10px;
    width: 100%;

}

#catalogueTitle{
    font-weight: 800;
}