.card-categories {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 250px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.5);
  border: 2px solid rgba(0, 0, 0, 0.2);
  padding: 50px 25px;
  overflow: hidden;
  border-radius: 10px;
  transition: ease 1s;
  background-color: rgb(221, 221, 221);
}

.content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 5px;
  color: #000000;
  transition: all 0.5s cubic-bezier(0.23, 1, 0.320, 1);
}

.content .heading {
  font-weight: 900;
  font-size: 20px;
  color: #000000;
}

.content .para {
  font-size: 15px;
}

.content .shopBtn {
  color: #e8e8e8;
  text-decoration: none;
  padding: 10px;
  font-weight: 600;
  border: none;
  cursor: pointer;
  background: linear-gradient(-45deg, #535f8d 0%, #272E48 100% );
  border-radius: 5px;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
}


.card-categories::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 5px;
  background: linear-gradient(-45deg, #535f8d 0%, #272E48 100% );
  z-index: -1;
  transition: all 0.5s cubic-bezier(0.23, 1, 0.320, 1);
}


.card-categories:hover::before {
  height: 100%;
}

.card-categories:hover {
  box-shadow: none;
  background: linear-gradient(-45deg, #535f8d 0%, #272E48 100%);
}

.card-categories:hover .shopBtn {
  color: #212121;
  background: #e8e8e8;
}

.content .shopBtn:hover {
  outline: 2px solid #e8e8e8;
  background: transparent;
  color: #e8e8e8;
}

.content .shopBtn:active {
  box-shadow: none;
}
