.checkout_container{
    border: 1px solid rgba(202, 202, 202, 1);
    margin-top: 75px;
    margin-bottom: 150px;
    padding: 40px;
    border-radius: 10px;
    background-color: #fff;
}

.cartquantityBtn{
    padding-left: 3px;
    padding-right: 3px;
    border-radius:3px;
    border: 1px solid lightgray;
    font-weight: 600;
    transition: 0.3s ease;
}

.cartquantityBtn:hover{
    background-color: lightgray;
}


/* styling for payment page */
::placeholder{
    opacity: 0.5;
    font-style: italic;
}

.edit_btn{
    border: none;
    background: none;
    font-size: 20px;
    font-weight: bold;
    display: inline-block;
    position: relative;
    color: #797979;
}

.edit_btn:hover{
    color: #3f3f3f;
}


#arrivalDate{
    font-size: 18px;
    margin-bottom: 15px;
}

#cartItem{
    margin-bottom: 20px;
}

.summary img{
    width: 50px;
    height: 50px;
}


.card{
    box-shadow: 0 10px 14px rgba(0, 0, 0, 0.2);
}

.card:hover{
    box-shadow: 0 10px 14px rgba(0, 0, 0, 0.5);
}